.gpt3__navbar {
    display: flex !important;
    justify-content: space-between;
    align-items: center;
    padding: 5rem 4rem;
    margin-top: 0;
    height: 56px;

    
  }
  .gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-top: 0px;
    margin-top: 0px;
    padding-bottom: 0px;
  }
  .gpt3__navbar-links_logo {
    margin-right: 1rem;
  }
  .logo-image{
     
    padding-top: 50px;
  }


  .gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
  }
  .gpt3__header-content__people p{
    color: #222222;
  }
  .gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
  }
  .gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .gpt3__navbar-links_container p,
  .gpt3__navbar-sign p,
  .gpt3__navbar-menu_container-links p {
    color: #222222;
    font-weight: 800;
    font-family: var(--font-family);
    
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
  }
  .gpt3__navbar-links_container p:hover,
  .gpt3__navbar-sign p:hover{
    color: #5280db;
  }
  .gpt3__navbar-sign button,
  .gpt3__navbar-menu_container-links button {
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    background: #5280db;
    border-radius: 5px;
    font-size: 18px;
    line-height: 25px;
    border: 0;
    outline: none;
    cursor: pointer;
  }
  .gpt3__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
  }
  .gpt3__navbar-menu svg {
    cursor: pointer;
  }
  .gpt3_navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
  }
  .gpt3__navbar-menu_container p {
    margin: 1rem 0;
  }
  .gpt3__navbar-menu_container-links-sign {
    display: none;
  }
  
  .logo-image img{
  width: 40vh;
  height: 40vh;
  }
  @media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
      display: none;
    }
    .gpt3__navbar-menu{
      display: flex;
    }
  }
  @media screen and (max-width: 700px) {
    .gpt3__navbar{
      padding: 2rem 4rem;
    }
  }
  @media screen and (max-width: 550px) {
    .gpt3__navbar{
      padding: 2rem ;
    }
    .gpt3__navbar-sign{
      display: none;
    }
    .gpt3__navbar-menu_container{
      top: 20px;
    }
    .gpt3__navbar-menu_container-links-sign{
      display: block;
    }
  }
  